import React from 'react';

import { ControlledInputTime, ControlledInputTimeProps } from './ControlledInputTime';
import { useInputTime } from './useInputTime';
import { TimeValue } from './utils';

export type InputTimeProps = Omit<ControlledInputTimeProps, 'value' | 'onChange'> & {
  /** Значение для компонента */
  value?: TimeValue;

  /** Значение внутри компонента изменилось */
  onChange?: (value?: TimeValue) => void;
};

export function InputTime({
  value,
  onChange,
  ...rest
}: InputTimeProps): JSX.Element {
  const [internalValue, setInternalValue] = useInputTime({ value, onChange });

  return (
    <ControlledInputTime
      { ...rest }
      value={ internalValue }
      onChange={ setInternalValue }
    />
  );
}
